import request from '@/utils/request'
import axios from 'axios'
let base = process.env.API_HOST//''///manage
/*
    区域管理
*/
export function getList(params) {//查询区域
  return request({
    url: base+'/vendingDistrict/list',
    method: 'get',
    params
  })
}
//托管公司查询
export function getCompany(params) {
  return request({
    url: base+'/corp/vaguelist',
    method: 'get',
    params
  })
}
//添加区域
export const addArea = params =>{
	  return axios.post(base + `/vendingDistrict/add`, params).then(res => res.data)
}
//编辑区域
export const editArea = params =>{
    return axios.post(base + `/vendingDistrict/edit`, params).then(res => res.data)
}

//删除区域
export const delArea = params =>{
    return axios.post(base + `/vendingDistrict/remove`, params).then(res => res.data)
}


