<template>
 <div class="div-warp">
        <el-dialog
          title="查看线路详情"
          :visible.sync="dialogVisible"
          width="50%"
          >
                 <el-form :model="editForm"  class="formWidth" >
                    <el-form-item label="线路编号" prop="code" size="small"  :label-width="formLabelWidth">
                      <el-input v-model="editForm.code" auto-complete="off" :disabled="true"></el-input>
                    </el-form-item>
                    <el-form-item label="线路名称" prop="name" size="small" :label-width="formLabelWidth">
                      <el-input v-model="editForm.name" auto-complete="off" :disabled="true"></el-input>
                    </el-form-item>

                    <el-form-item label="区域名称" prop="districtId" size="small" :label-width="formLabelWidth">
                      <el-input v-model="editForm.districtName" auto-complete="off" :disabled="true"></el-input>
                    </el-form-item>
                    <el-form-item label="描述"  size="small" :label-width="formLabelWidth">
                      <el-input type="textarea" v-model="editForm.description" :disabled="true"></el-input>
                    </el-form-item>
                  </el-form>
      </el-dialog>
      <div class="search-warp">
        <el-row :gutter="20" >
            <el-col :span="4">
              <div class="grid-content bg-purple">
                  <el-input
                  placeholder="请输入线路"
                  v-model="keyword"
                  clearable>
                </el-input>

              </div>
            </el-col>
            <el-col :span="16">
                <div class="grid-content bg-purple"> 
                  <el-button type="success" @click.native.prevent="search">查询</el-button>
                  <el-button type="primary" @click="handleAdd">新增</el-button>
                  <el-button type="danger" @click="deleteRow">删除</el-button>
                </div>
            </el-col>
            <el-col :span="4"><div class="grid-content bg-purple"></div></el-col>
        </el-row>
      </div>
      <div class="block-warp">
      <wTable :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)" :header="tableHeader" :handleSelectionChange="handleSelectionChange" :option="tableOption">
        <el-table-column slot="fixed"
          fixed="right"
          label="操作"
          width="150">
            <template  scope="scope">
              <div style="text-align:center;">
                <el-button type="success" size="mini" icon="el-icon-view" circle @click="handdetails(scope.row)"></el-button>
                <el-button type="primary" size="mini"icon="el-icon-edit" circle @click="handleEdit(scope.$index, scope.row)"></el-button>
                <el-button type="danger"  size="mini" icon="el-icon-delete" circle @click.native.prevent="deleteRow(scope.row)"></el-button>
              </div>
            </template>
        </el-table-column>
      </wTable>
        
      <div class="pageBox">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          background
          stripe 
          :page-size="pagesize"
          layout="prev, pager, next"
          :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
import { getLineList,delLine } from '@/api/pointposition'
import NProgress from 'nprogress'
import wTable from '@/components/table/w-table.vue'
export default {
  data() {
        return {
          total:1,
          currentPage:1, 
          pagesize:10, 
          formLabelWidth: '120px',
          dialogVisible:false,
          keyword:'',
          multipleSelection:"",
          tableOption:{
            border: true
          },
          tableHeader: [
              {
                type:"selection",
                width:"55"
              },
              {
                prop: 'code',
                label: '线路编号 ',
                width: '78',
                sortable: true,
                sortMethod: this.handleNameSort
              },
              {
                prop: 'name',
                label: '线路名称',
                 minWidth: '200'
             
              },
              {
                prop: 'districtName',
                label: '区域名称',
                minWidth: '120'
              },
              {
                prop: 'description',
                label: '描述'
              },
              {
                prop: 'createTime',
                label: '创建时间',
                minWidth: '120'
              }
          ],
          tableData: [],
          filterText: '',
          editForm:{}
        }
  },
  watch: {
    filterText(val) {
      this.$refs.tree2.filter(val)
    }
  },
  methods: {
    handdetails(row){//查看
      this.dialogVisible = true
      this.editForm = Object.assign({}, row);
    },
     handleSizeChange: function (size) { 
   
     this.pagesize = size; 
     }, 
     handleCurrentChange: function(currentPage){ 

     this.currentPage = currentPage;
     this.getData(currentPage)
     } ,
    handleSelectionChange(rows) {//全选
        const _this = this;
        _this.multipleSelection = ""
        if (rows) {
          rows.forEach(row => {
             _this.multipleSelection += row.logid+ ",";
      
          });
        } 

    },
    search(){
      this.getData("")
    },
    deleteRow(row) {//删除数据
      const _this  = this;
      this.$confirm('确认提交吗？', '提示', {}).then(() => {
                  let para ={
                    ids:_this.multipleSelection=="" ? row.logid : _this.multipleSelection
                  }
                  delLine(para).then((res) => {
                        if(res.zhead.reTCode=="0000"){
                        NProgress.done();
                        this.$notify({
                          title: '成功',
                          message: res.zhead.retMsg,
                          type: 'success'
                        });
                        _this.getData("")
                        //rows.splice(index, 1);
                        }else{
                          this.$notify({
                            title: '失败',
                            message: res.zhead.retMsg,
                            type: 'error'
                          });
                  }
              });

          

      });
    },
    handleAdd() {//显示新增窗口
      this.$router.push(
          {path:'/pointposition/line/add'}
      );
      //this.dialogAddTableVisible= true
    },
    handleEdit(index, row) {//显示编辑窗口{
          if(window.localStorage){
            localStorage.setItem("editData",JSON.stringify(row) );
          }else{

          }
          this.$router.push(
              {path:'/pointposition/line/edit'}
          );
    // this.dialogEditTableVisible= true
     this.editForm = Object.assign({}, row);
    },
    handleNameSort () {
      console.log('handleNameSort')
    },
    getData(page) {
      NProgress.start();
      var _this = this;
      this.listLoading = true
      const listQuery = {
        name:_this.keyword,
        pageSize:page,
        pageNum:'',
        orderByColumn:'createTime',
        isAsc:'asc'//desc
      }
      getLineList(listQuery).then(response => {

         if(response.zhead.reTCode==="0000"){
            NProgress.done();
          _this.total = response.zbody.datas.total;  
          _this.tableData = response.zbody.datas.rows;
          console.log(_this.tableData)
         }else{
             this.$notify({
              title: '失败',
              message: res.zhead.retMsg,
              type: 'error'
            });
         }
       
      })
    }
  },

  mounted () {
    this.getData("")
  },
  components: {
    wTable
  }
}
</script>
<style scoped>
  .div-warp{padding:0 20px;}
  .pageBox{background: #fff; padding:20px 0;}

</style>